<h1 id="python-dicts" tabindex="-1"><a class="header-anchor" href="#python-dicts" aria-hidden="true">#</a> Python Dicts</h1>
<p>A <em>dict</em> is a value in Python that stores multiple values. In that sense, a dict it is quite similar to a list. The main difference is that each value in a list gets an index (<code>0</code>, <code>1</code>, <code>2</code>, ...), but in a dict each value gets a key instead, such as <code>name</code>, <code>age</code>, etc. Let's take a closer look at dicts works in this lecture.</p>
